  export default {
	data (){
		return {
            title_link: {
                title: '帮助中心',
                link: '#/list_help'
            },
            tab:[
                {
                    link: '#/list_article?list=help&id=1',
                    title: '购物指南'
                },
                {
                    link: '#/list_article?list=help&id=2',
                    title: '支付帮助'
                },
                {
                    link: '#/list_article?list=help&id=3',
                    title: '配送说明'
                },
                {
                    link: '#/list_article?list=help&id=4',
                    title: '售后服务'
                },
            ],
            help: 
            {
                title: '走时精准样子符合年轻人正品保障不错的5星',
                author: 'admin',
                date: '2016-11-06',
                views: 272,
                info: [
                    '我国工具五金行业历经30多年发展，工具产品的制造在世界上占有突出地位，已成为全球工具产品的生产和销售大国。行业销售额超过1000亿元，出口超过140亿美元。从产业发展深层次角度分析，工具五金行业在经历了高速发展期后，已形成了较强的制造能力和一定的营销能力，未来产业将进一步向高端、专业、精品领域拓展，并向服务型制造升级。',
                    '跟着社会经济的成长，近年来，我国已成为世界五金出产大国、世界五金加工大国和出口大国。我国具有广漠的市场和消费潜力，在越绿色越挣钱的“十二五”期间，节能、智能化产物大行其道，五金电动工具代理家居橱柜五金行业出现新的趋向。我国已成为世界五金出产大国之一，具有广漠的市场和消费潜力。在中国五金财产中至多有70%为民营企业，为中国五金行业成长的主力军。国际五金市场上：泰西发财国度因为出产手艺倏地成长与劳动力价钱升高，将转由成长中国度出产高附加价值的产物。',
                    '我国五金产物品种繁多、质量优秀、价位正当，凭仗经销商优良的诺言、国内五金工具品牌殷勤的办事和多年来堆集沉淀的复杂发卖收集群，已逐步打建国内各省市及中东、非洲、哈萨克斯坦、南亚、乌拉圭等新兴市场。此中一部门五金产物的机能，已到达或靠近国际先辈程度，这个进展无效地攻破了一些进口五金产物在一些范畴的垄断职位地方，为我国五金市场的规范合作作出了孝敬.',
                    '五金工具是劳动稠密型财产，作为五金之乡的永康，更是凭仗低本钱的保守合作劣势，一度成为五金工具的集聚之地。永康五金市场曾经构成五金产物、电动工具、机器设施等16类买卖区，搜集了日用五金、修建五金、工具五金及机电产物、金属资料、机器设施、粉饰资料等上万种五金产物及有关产物，商品辐射国内及俄罗斯、美国、、巴西、、日本等50多个国度和地域。2002-2004年市场成交额别离为126亿元、15元和192亿元。五金城的成长繁荣，为永康五金财产起飞架起了顺利的桥梁，进一步鞭策了区域经济成长，实现了经济效益和社会效益双丰收。',
                    '进入新世纪以来，跟着我国制作业加快当代(当代装修结果图)化的程序，相关业内人士预测，中国五金工具企业的成长一定有大的动作才能见分晓,不然差同化不较着带来的系列问题会迟延行业的升级,巨头的呈现是一定,合适市场经济的纪律和财产成长的特点。'
                ]
            }
        }
	},
    computed : {
        update (){
            console.log('msg='+ this.$route.query.msg + ',id='+ this.$route.query.id);
            if (this.$route.query.msg == 'help') {
              this.title_link = {
                title: '帮助中心',
                link: '#/list_help'
              }
            }else if(this.$route.query.msg == 'experience'){
              this.title_link = {
                title: '晒单',
                link: '#/list_experience'
              }
            }
        }
    },
    created (){
        this.update;
    },
    watch: {
        $route(){
            this.update;
        }
    }
  }



